<template>
  <div id="metEdit">
    <el-row class="warp">
      <el-col
        :span="24"
        class="warp-main"
        v-loading="loading"
        element-loading-text=""
      >
        <!---->
        <el-col
          :span="24"
          class="toolbar"
          style="padding-bottom: 0px;"
        >
          <el-form
            :inline="true"
            :model="filters"
          >
            <el-form-item>
              <el-input
                v-model="filters.content"
                placeholder=""
                @keyup.enter.native="handleSearch"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button
                type="primary"
                @click="handleSearch"
              ></el-button>
            </el-form-item>
            <el-form-item>
              <el-button
                type="primary"
                @click="showAddDialog"
              ></el-button>
            </el-form-item>
          </el-form>
        </el-col>

        <!---->
        <el-table
          :data="items"
          highlight-current-row
          @selection-change="selsChange"
          style="width: 100%; margin-bottom:10px;"
        >
          <el-table-column
            type="selection"
            width="55"
          ></el-table-column>
          <el-table-column
            type="index"
            width="60"
          ></el-table-column>
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form
                label-position="left"
                inline
                class="demo-table-expand"
              >
                <el-form-item label="[]">
                  <img :src="props.row.img" class="avatar2">
                </el-form-item>
                <el-form-item label="[]">
                  <div v-html="props.row.introduction"></div>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column
            prop="title"
            label=""
            sortable
          ></el-table-column>
          <el-table-column
            prop="host"
            label=""
            width="220"
            sortable
          ></el-table-column>
          <el-table-column
            prop="address"
            label=""
            width="180"
            sortable
          ></el-table-column>
          <el-table-column
            prop="holdTime"
            label=""
            width="150"
            sortable
          ></el-table-column>
          <el-table-column
            label=""
            width="200"
          >
            <template slot-scope="scope">
              <el-button
                size="small"
                @click="showEditDialog(scope.$index,scope.row)"
              ></el-button>
              <el-button
                type="danger"
                @click="delItem(scope.$index,scope.row)"
                size="small"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>

        <!---->
        <el-col
          :span="24"
          class="toolbar"
        >
          <el-button
            type="danger"
            @click="batchDeleteItems"
            :disabled="this.sels.length===0"
          ></el-button>
          <el-pagination
            layout="prev, pager, next"
            background
            @current-change="handleCurrentChange"
            :page-size="pageSize"
            :total="total"
            style="float:right;"
          >
          </el-pagination>
        </el-col>

        <!--  -->
        <el-dialog
          title=""
          :visible.sync="editFormVisible"
          :close-on-click-modal="true"
          top="4vh"
          :modal-append-to-body="false"
        >
          <el-form
            :model="editForm"
            label-width="100px"
            :rules="editFormRules"
            ref="editForm"
          >
            <el-form-item
              label=""
              prop="title"
            >
              <el-input
                v-model="editForm.title"
                auto-complete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label=""
              prop="host"
            >
              <el-input
                v-model="editForm.host"
                auto-complete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label=""
              prop="address"
            >
              <el-input
                v-model="editForm.address"
                auto-complete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label=""
              prop="holdTime"
            >
              <el-date-picker
                type="date"
                placeholder=""
                v-model="editForm.holdTime"
              ></el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="img">
              <el-upload
                class="avatar-uploader"
                action="http://122.13.4.239/common/uploadImg"
                :http-request="editImgUpload"
                :auto-upload="true"
                :show-file-list="false"
                :before-upload="beforeAvatarUpload">
                <img v-if="editCoverShow" :src="editForm.img" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item
              label=""
              prop="introduction"
            >
              <el-input
                type="textarea"
                v-model="addForm.introduction"
                :rows="8"
              ></el-input>
            </el-form-item>
          </el-form>
          <div
            slot="footer"
            class="dialog-footer"
          >
            <el-button @click.native="editFormVisible = false"></el-button>
            <el-button
              type="primary"
              @click.native="editSubmit"
            ></el-button>
          </div>
        </el-dialog>

        <!---->
        <el-dialog
          title=""
          :visible.sync="addFormVisible"
          :close-on-click-modal="true"
          top="4vh"
          :modal-append-to-body="false"
        >
          <el-form
            :model="addForm"
            label-width="80px"
            :rules="addFormRules"
            ref="addForm"
          >
            <el-form-item
              label=""
              prop="title"
            >
              <el-input
                v-model="addForm.title"
                auto-complete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label=""
              prop="host"
            >
              <el-input
                v-model="addForm.host"
                auto-complete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label=""
              prop="address"
            >
              <el-input
                v-model="addForm.address"
                auto-complete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label=""
              prop="holdTime"
            >
              <el-date-picker
                type="date"
                placeholder=""
                v-model="addForm.holdTime"
              ></el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="img">
              <el-upload
                class="avatar-uploader"
                action="http://122.13.4.239/common/uploadImg"
                :http-request="addImgUpload"
                :auto-upload="true"
                :show-file-list="false"
                :before-upload="beforeAvatarUpload">
                <img v-if="addCoverShow" :src="addForm.img" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item
              label=""
              prop="introduction"
            >
              <el-input
                type="textarea"
                v-model="addForm.introduction"
                :rows="8"
              ></el-input>
            </el-form-item>
          </el-form>
          <div
            slot="footer"
            class="dialog-footer"
          >
            <el-button @click.native="addFormVisible = false"></el-button>
            <el-button
              type="primary"
              @click.native="addSubmit"
              :loading="addLoading"
            ></el-button>
          </div>
        </el-dialog>

      </el-col>
    </el-row>
  </div>
</template>
<script>
import API from "@/api/api_admin";
import Qs from "qs";
import { VueEditor } from "vue2-editor";

export default {
  name: "metEdit",
  components: { VueEditor },
  props: ["currentTab", "isRequested"],
  data() {
    return {
      categoryId: 0,
      timeType: 0,
      //
      filters: {
        content: ""
      },
      //
      items: [
        {
          id: "",
          title: "",
          host: "",
          holdTime: "",
          address: "",
          introduction: "",
          img: null
        }
      ],
      total: 1, //
      pageSize: 12, //
      page: 1, //
      // limit: 10,
      loading: false, //loading  
      sels: [], //
      //
      //categoryId
      initFormRules: [
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "get",
          categoryId: 6
        },
        {
          name: "",
          //method: "get",
          categoryId: 12
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "get",
          categoryId: 7
        },
        {
          name: "",
          //method: "get",
          categoryId: 9
        },
        {
          name: "",
          //method: "get",
          categoryId: 10
        },
        {
          name: "",
          //method: "get",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "get",
          categoryId: 5
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "get",
          categoryId: 8
        },
        {
          name: "",
          //method: "",
          categoryId: 2
        },
        {
          name: "",
          //method: "",
          categoryId: 1
        },
        {
          name: "",
          //method: "",
          categoryId: 3
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: 4
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "get",
          categoryId: ""
        },
        {
          name: "",
          //method: "get",
          categoryId: 11
        },
        {
          name: "",
          //method: "get",
          categoryId: ""
        },
        // categoryId:1  21
        // timeType       1  2  3
        {
          name: "",
          //method: "",
          categoryId: "1",
          timeType: "1"
        },
        {
          name: "",
          //method: "",
          categoryId: "2",
          timeType: "2"
        },
        {
          name: "",
          //method: "",
          categoryId: "2",
          timeType: "3"
        },
        {
          name: "",
          //method: "",
          categoryId: "1"
        },
        {
          name: "",
          //method: "",
          categoryId: "1",
          timeType: "2"
        },
        {
          name: "",
          //method: "",
          categoryId: "1",
          timeType: "3"
        },
        {
          name: "1",
          //method: "",
          categoryId: ""
        },
        {
          name: "2",
          //method: "",
          categoryId: ""
        },
        {
          name: "3",
          //method: "",
          categoryId: ""
        }
      ],
      customToolbar: [
        // [{ 'size': ['small', false, 'large', 'huge'] }],
        [{ header: [false, 1, 2, 3, 4, 5, 6] }],
        ["bold", "italic", "underline", "strike"],
        ["clean"],
        [
          { align: "" },
          { align: "center" },
          { align: "right" },
          { align: "justify" }
        ],
        [{ list: "ordered" }, { list: "bullet" }],
        [{ indent: "-1" }, { indent: "+1" }],
        ["blockquote", "code-block"],
        ["image", "link"]
      ],
      //
      editCoverShow:false,//
      editFormVisible: false, //
      // 
      editFormRules: {
        title: [{ required: true, message: "", trigger: "blur" }],
        host: [{ required: true, message: "", trigger: "blur" }],
        address: [{ required: false, message: "", trigger: "blur" }],
        introduction: [
          { required: false, message: "", trigger: "blur" }
        ],
        holdTime: [{ required: true, message: "", trigger: "blur" }]
      },
      //refdom
      editForm: {
        id: 0,
        title: "",
        host: "",
        address: "",
        holdTime: "",
        introduction: {},
        img:""
      },
      //
      addCoverShow:false,//
      addFormVisible: false, //
      addLoading: false,
      // 
      addFormRules: {
        title: [{ required: true, message: "", trigger: "blur" }],
        host: [{ required: true, message: "", trigger: "blur" }],
        address: [{ required: false, message: "", trigger: "blur" }],
        introduction: [
          { required: false, message: "", trigger: "blur" }
        ],
        holdTime: [{ required: true, message: "", trigger: "blur" }]
      },
      //refdom
      addForm: {
        title: "",
        host: "",
        address: "",
        holdTime: "",
        introduction: "",
        img:"",
      }
    };
  },
  methods: {
    //
    handleCurrentChange(val) {
      this.page = val;
      this.search();
    },
    //
    beforeAvatarUpload(file) {
      // const isJPG = file.type === 'image/jpeg';
      const isLt10M = file.size / 1024 /1024 < 10;
      // if (!isJPG) {
      //   this.$message.error(' JPG !');
      // }
      if (!isLt10M) {
        this.$message.error(' 10MB!');
      }
      // return isJPG && isLt10M;
    },
    //
    //
    showAddDialog() {
      this.addFormVisible = true;
      this.addForm = {
        title: "",
        host: "",
        address: "",
        holdTime: "",
        introduction: ""
      };
    },
    //
    addImgUpload(e){
      let that = this;
        // let params = Qs.stringify({
        //     imgs: file,
        //   });
        let url = that.HOST +"/common/uploadImg"
        let formData = new FormData();
        formData.append("imgs", e.file);
        that.axios({
          url: url,
          method: 'POST',
          headers: {'Content-Type': 'multipart/form-data'},
          data: formData
        })
        .then(response => {
          if(response.data.result == "success"){
            that.$message.success({
              showClose: true,
              message: "",
              duration: 2000
          });
          that.addCoverShow = true;
          that.addForm.img = response.data.message[0];
          }
          else{
            that.$message.error({
              showClose: true,
              message: "",
              duration: 2000
          });
          }
        })
        .catch((err) => {
          console.log(err);
          that.$message.error({
            showClose: true,
            message: "",
            duration: 2000
          });
        })
    },
    //
    addSubmit() {
      let that = this;
      let para = Object.assign({}, that.addForm);
      if (para.title && para.holdTime && para.host) {
        that.$confirm("", "", { type: "warning" }).then(() => {
          let url = that.HOST + "/conference/addConference";
          let params = Qs.stringify({
            title: para.title,
            address: para.address,
            img:para.img,
            hold_time: para.holdTime,
            introduction:para.introduction,
            conference_type: that.categoryId
          });
          that.loading = true;
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(function(response) {
              that.loading = false;
              if (response.data.result == "success") {
                that.$message.success({
                  showClose: true,
                  message: "",
                  duration: 1500
                });
                that.addFormVisible = false;
                that.search();
              } else {
                let err = response.data.message;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
                that.addFormVisible = false;
              }
            })
            .catch(response => {
              that.loading = false;
              that.$message.error({
                showClose: true,
                message: "",
                duration: 2000
              });
            });
        });
      } else {
        that.$message.warning({
          showClose: true,
          message: "",
          duration: 1000
        });
      }
    },

    //
    delItem(index, row) {
      let that = this;
      this.$confirm("?", "", {
        type: "warning"
      })
        .then(() => {
          that.loading = true;
          let postId = row.id;
          let url = that.HOST + "/information/manager/deleteInformation";
          let params = Qs.stringify({
            ids: postId
          });
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(response => {
              if (response.data.result == "success") {
                that.loading = false;
                that.$message.success({
                  showClose: true,
                  message: "",
                  duration: 1500
                });
                that.search();
              } else {
                let err = response.data.message;
                that.loading = false;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
              }
            })
            .catch(function(error) {
              that.loading = false;
              console.log(error);
              that.$message.error({
                showClose: true,
                message: "",
                duration: 2000
              });
            });
        })
        .catch(() => {});
    },
    //
    selsChange: function(sels) {
      this.sels = sels;
    },
    //
    batchDeleteItems() {
      let that = this;
      this.$confirm("", "", {
        type: "warning"
      })
        .then(() => {
          that.loading = true;
          let ids = that.sels.map(item => item.id).toString();
          let url = that.HOST + "/information/manager/deleteInformation";
          let params = Qs.stringify({
            ids: ids
          });
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(response => {
              if (response.data.result == "success") {
                that.loading = false;
                that.$message.success({
                  showClose: true,
                  message: "",
                  duration: 1500
                });
                that.search();
              } else {
                let err = response.data.message;
                that.loading = false;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
              }
            })
            .catch(function(error) {
              that.loading = false;
              console.log(error);
              that.$message.error({
                showClose: true,
                message: "",
                duration: 2000
              });
            });
        })
        .catch(() => {});
    },

    //
    handleSearch() {
      this.total = 0;
      this.page = 1;
      this.search();
    },
    //
    search() {
      // console.log(this.currentTab);
      let that = this;

      let index = that.initFormRules.findIndex(
        menu => menu.name === that.currentTab
      );
      // console.log(index);
      let url = that.HOST + "/conference/findConference";
      that.categoryId = that.initFormRules[index].categoryId;
      that.timeType = that.initFormRules[index].timeType;
      let content = that.filters.content.trim();

      if (that.categoryId != "") {
        let data = {
          pageNum: that.page, //1
          categoryId: that.categoryId, //,
          content: content,
          timeType: that.timeType
        };
        //     //loading
        that.loading = true;
        //     //get
        that.axios
          .get(url, {
            params: data
          })
          .then(response => {
            that.items = response.data.message.list;
            that.items.forEach((item, index) => {
              that.items[index].holdTime = item.holdTime.slice(0, 10);
            });
            that.total = response.data.message.total;
            that.pageSize = response.data.message.pageSize;
            that.loading = false;
          })
          .catch(response => {
            that.loading = false;
            that.items = [];
            that.total = 0;
            that.$message.error({
              showClose: true,
              message: "",
              duration: 2000
            });
          });
      } else {
        that.items = [];
        that.total = 0;
        that.$message.error({
          showClose: true,
          message: "",
          duration: 2000
        });
      }
    },

    //
    //
    showEditDialog(index, row) {
      this.editFormVisible = true;
      if(row.img){that.editCoverShow=true;}
      this.editForm = Object.assign({}, row);
    },
    editImgUpload(e){
      let that = this;
      // let params = Qs.stringify({
      //     imgs: file,
      //   });
      let url = that.HOST +"/common/uploadImg"
      let formData = new FormData();
      formData.append("imgs", e.file);
      that.axios({
        url: url,
        method: 'POST',
        headers: {'Content-Type': 'multipart/form-data'},
        data: formData
      })
      .then(response => {
        if(response.data.result == "success"){
          that.editCoverShow = true;
          that.editForm.img = response.data.message;
        }
        else{
          that.$message.error({
            showClose: true,
            message: "",
            duration: 2000
        });
        }
      })
      .catch((err) => {
        console.log(err);
        that.$message.error({
          showClose: true,
          message: "",
          duration: 2000
        });
      })      
    },
  },
  activated() {
    this.handleSearch();
  },
  watch: {
    //
    isRequested() {
      this.handleSearch();
      // console.log(this.isRequested);
    }
  }
};
</script>
<style lang="scss" scoped>
.el_b_form input{
  width: 50%!important;
}
.editor1 #quill-container{
  max-height: 1000px;
  overflow: auto;
}
.ql-snow .ql-picker{
  height: auto!important;
  margin-top:-6px; 
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 108px;
  height: 108px;
  line-height: 108px;
  text-align: center;
}
.avatar {
  width: 108px;
  height: 108px;
  display: block;
}
</style>


