<template>
  <div id="artEdit">
    <el-row class="warp">
      <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="">
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
          <el-form :inline="true" :model="filters">
            <el-form-item>
              <el-input v-model="filters.content" placeholder="" @keyup.enter.native="handleSearch"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @keyup="keyUpSubmit($event)" @click="handleSearch"></el-button>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="showAddDialog"></el-button>
            </el-form-item>
          </el-form>
        </el-col>

        <!---->
        <el-table :data="items" highlight-current-row @selection-change="selsChange" style="width: 100%; margin-bottom:10px;">
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column type="index" width="60"></el-table-column>
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label="[]">
                  <img :src="props.row.img" class="avatar2">
                </el-form-item>
                <el-form-item label="[]">
                  <div v-html="props.row.readGuide"></div>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column prop="title" label="" sortable></el-table-column>
          <el-table-column prop="source" label="" width="100" sortable></el-table-column>
          <el-table-column prop="postDate" label="" width="150" sortable></el-table-column>
          <el-table-column label="" width="200">
            <template slot-scope="scope">
              <el-button size="small" @click="showEditDialog(scope.$index,scope.row)"></el-button>
              <el-button type="danger" @click="delItem(scope.$index,scope.row)" size="small"></el-button>
            </template>
          </el-table-column>
        </el-table>

        <!---->
        <el-col :span="24" class="toolbar">
          <el-button type="danger" @click="batchDeleteItems" :disabled="this.sels.length===0"></el-button>
          <el-pagination layout="prev, pager, next" background @current-change="handleCurrentChange" :page-size="pageSize" :total="total" style="float:right;">
          </el-pagination>
        </el-col>

        <!--  -->
        <el-dialog title="" :visible.sync="editFormVisible" top="4vh" :close-on-click-modal="true" :modal-append-to-body="false">
          <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
            <el-form-item label="" prop="title">
              <el-input v-model="editForm.title" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="" prop="source">
              <el-input v-model="editForm.source" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="" prop="postDate">
              <el-date-picker type="date" placeholder="" v-model="editForm.postDate"></el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="img">
              <el-upload
                class="avatar-uploader"
                action="http://122.13.4.239/common/uploadImg"
                :http-request="editImgUpload"
                :auto-upload="true"
                :show-file-list="false"
                :before-upload="beforeAvatarUpload">
                <img v-if="editCoverShow" :src="editForm.img" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item label="" prop="content">
              <vue-editor class="editor1" useCustomImageHandler @imageAdded="handleImageAdded" v-for="(item,index) of editFormContent" :editorToolbar="customToolbar" v-show="index+1==editCurrentPage" :key="index" v-model="item.content"></vue-editor>
              <!-- <el-input type="textarea" v-model="editForm.content[0].content" :rows="8"></el-input> -->
              <el-pagination style="margin-top;20px;" layout="prev, pager, next" :total="editFormContent.length" :page-size=1 :current-page.sync="editCurrentPage">
             </el-pagination>   
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click.native="editFormVisible = false"></el-button>
            <el-button type="primary" @click.native="editSubmit"></el-button>
          </div>
        </el-dialog>

        <!---->
        <el-dialog title="" :visible.sync="addFormVisible" top="4vh" :close-on-click-modal="true" :modal-append-to-body="false">
          <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
            <el-form-item label="" prop="title">
              <el-input v-model="addForm.title" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="" prop="source">
              <el-input v-model="addForm.source" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="" prop="postDate">
              <el-date-picker type="date" placeholder="" v-model="addForm.postDate"></el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="img">
              <el-upload
                class="avatar-uploader"
                action="http://122.13.4.239/common/uploadImg"
                :http-request="addImgUpload"
                :auto-upload="true"
                :show-file-list="false"
                :before-upload="beforeAvatarUpload">
                <img v-if="addCoverShow" :src="addForm.img" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item label="" prop="content" :row="20">
              <vue-editor class="editor1" useCustomImageHandler @imageAdded="handleImageAdded" v-for="(item,index) of addFormContent" :editorToolbar="customToolbar" v-show="index+1==addCurrentPage" :key="index" v-model="item.content"></vue-editor>
              <!-- <el-input type="textarea" v-model="addForm..content[0].content" :rows="8"></el-input> -->
              <el-pagination style="margin-top;20px;" layout="prev, pager, next" :total=5 :page-size=1 :current-page.sync="addCurrentPage">
             </el-pagination>   
           </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click.native="addFormVisible = false"></el-button>
            <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
          </div>
        </el-dialog>

      </el-col>
    </el-row>
  </div>
</template>
<script>
import API from "@/api/api_admin";
import Qs from "qs";
import { VueEditor } from 'vue2-editor';

export default {
  name: "artEdit",
  props: ["currentTab", "isRequested"],
  data() {
    return {
      categoryId: 0,
      //
      filters: {
        content: ""
      },
      //
      items: [
        // {
        //   id: "",
        //   title: "",
        //   postDate: "",
        //   source: "",
        //   content: [
        //   {
        //     page: 1,
        //     content: ""
        //   }
        // ]
        // }
      ],
      total: 1, //
      pageSize: 12, //
      page: 1, //
      // limit: 10,
      loading: false, //loading  
      sels: [], //
      //
      //categoryId
      initFormRules: [
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "get",
          categoryId: 6
        },
        {
          name: "",
          //method: "get",
          categoryId: 12
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "get",
          categoryId: 7
        },
        {
          name: "",
          //method: "get",
          categoryId: 9
        },
        {
          name: "",
          //method: "get",
          categoryId: 10
        },
        {
          name: "",
          //method: "get",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "get",
          categoryId: 5
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "get",
          categoryId: 8
        },
        {
          name: "",
          //method: "",
          categoryId: 2
        },
        {
          name: "",
          //method: "",
          categoryId: 1
        },
        {
          name: "",
          //method: "",
          categoryId: 3
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: 4
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "get",
          categoryId: ""
        },
        {
          name: "",
          //method: "get",
          categoryId: 11
        },
        {
          name: "",
          //method: "get",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "1",
          //method: "",
          categoryId: ""
        },
        {
          name: "2",
          //method: "",
          categoryId: ""
        },
        {
          name: "3",
          //method: "",
          categoryId: ""
        }
      ],
      customToolbar: [
        // [{ 'size': ['small', false, 'large', 'huge'] }],  
        [{ 'header': [false,1, 2, 3, 4, 5, 6] }], 
        ['bold', 'italic', 'underline','strike'],['clean'],
        [{ 'align':""},{ 'align':"center"},{ 'align':"right"},{ 'align':"justify"}],
        [{ 'list': 'ordered'}, { 'list': 'bullet' }],
        [{ 'indent': '-1'}, { 'indent': '+1' }],
        ['blockquote', 'code-block'],
        ['image','link', ], 
      ],
      //
      editCurrentPage:1,//
      editFormVisible: false, //
      // 
      editFormRules: {
        title: [{ required: true, message: "", trigger: "blur" }],
        source: [{ required: false, message: "", trigger: "blur" }],
        content: [{ required: true, message: "", trigger: "blur" }],
        postDate: [{ required: false, message: "", trigger: "blur" }]
      },
      //
      editFormContent:[
        {
          page: 1,
          content: ""
        },
        {
          page: 2,
          content: ""
        },
        {
          page: 3,
          content: ""
        },
        {
          page: 4,
          content: ""
        },
        {
          page: 5,
          content: ""
        }
      ],
      //
      editCoverShow:false,
      //refdom
      editForm: {
        id: 0,
        title: "",
        source: "",
        postDate: "",
        img:"",
        content: [
          {
            page: 1,
            content: ""
          }
        ]
      },
      //
      addCurrentPage:1,//
      addFormVisible: false, //
      addLoading: false,
      // 
      addFormRules: {
        title: [{ required: true, message: "", trigger: "blur" }],
        source: [{ required: false, message: "", trigger: "blur" }],
        content: [{ required: false, message: "", trigger: "blur" }],
        postDate: [{ required: false, message: "", trigger: "blur" }]
      },
      //
      addFormContent:[
        {
          page: 1,
          content: ""
        },
        {
          page: 2,
          content: ""
        },
        {
          page: 3,
          content: ""
        },
        {
          page: 4,
          content: ""
        },
        {
          page: 5,
          content: ""
        }
      ],
      //
      addCoverShow:false,
      //refdom
      addForm: {
        title: "",
        source: "",
        postDate: "",
        img:"",
      }
    };
  },
  methods: {
    //
    handleCurrentChange(val) {
      this.page = val;
      this.search();
    },
    //
    handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      console.log(file)
        let that = this;
        // let params = Qs.stringify({
        //     imgs: file,
        //   });
        let url = that.HOST +"/common/uploadImg"
        let formData = new FormData();
        formData.append("imgs", file);
        that.axios({
          url: url,
          method: 'POST',
          headers: {'Content-Type': 'multipart/form-data'},
          data: formData
        })
        .then(response => {
          if(response.data.result == "success"){
            let url = response.data.message // Get url from response
            // console.log(url)
            Editor.insertEmbed(cursorLocation, 'image', url[0]);
            // resetUploader();
          }
          else{
            that.$message.error({
              showClose: true,
              message: "",
              duration: 2000
          });
          }
        })
        .catch((err) => {
          console.log(err);
          that.$message.error({
            showClose: true,
            message: "",
            duration: 2000
          });
        })
    },
    //
    beforeAvatarUpload(file) {
      // const isJPG = file.type === 'image/jpeg';
      const isLt10M = file.size / 1024 /1024 < 10;
      // if (!isJPG) {
      //   this.$message.error(' JPG !');
      // }
      if (!isLt10M) {
        this.$message.error(' 10MB!');
      }
      // return isJPG && isLt10M;
    },
//
    //
    showAddDialog() {
      this.addFormVisible = true;
      this.addForm =Object.assign(this.addForm, {
        postDate: "",
      })
      this.addFormContent=[
          {
            page: 1,
            content: ""
          },
          {
            page: 2,
            content: ""
          },
          {
            page: 3,
            content: ""
          },
          {
            page: 4,
            content: ""
          },
          {
            page: 5,
            content: ""
          }
        ]
    },
    //
    addImgUpload(e){
      let that = this;
        // let params = Qs.stringify({
        //     imgs: file,
        //   });
        let url = that.HOST +"/common/uploadImg"
        let formData = new FormData();
        formData.append("imgs", e.file);
        that.axios({
          url: url,
          method: 'POST',
          headers: {'Content-Type': 'multipart/form-data'},
          data: formData
        })
        .then(response => {
          if(response.data.result == "success"){
            that.$message.success({
              showClose: true,
              message: "",
              duration: 2000
          });
          that.addCoverShow = true;
           that.addForm.img = response.data.message[0];
          }
          else{
            that.$message.error({
              showClose: true,
              message: "",
              duration: 2000
          });
          }
        })
        .catch((err) => {
          console.log(err);
          that.$message.error({
            showClose: true,
            message: "",
            duration: 2000
          });
        })
    },
    //
    addSubmit() {
      let that = this;
      let para = Object.assign({}, that.addForm);
      if (para.title && that.addFormContent[0].content) {
        that.$confirm("", "", { type: "warning" }).then(() => {
          let url = that.HOST + "/information/manager/addInformation";
          let params = Qs.stringify({
            title: para.title,
            source: para.source,
            img:para.img,
            // postDate: para.postDate,
            categoryId: that.categoryId,
            content:that.addFormContent
          },{arrayFormat: 'indices', allowDots: true});
          that.loading = true;
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(function(response) {
              that.loading = false;
              that.addCoverShow = false;
              if (response.data.result == "success") {
                that.$message.success({
                  showClose: true,
                  message: "",
                  duration: 1500
                });
                that.addFormVisible = false;
                that.search();
              } else {
                let err = response.data.message;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
                that.addFormVisible = false;
              }
            })
            .catch(response => {
              that.loading = false;
              that.$message.error({
                showClose: true,
                message: "",
                duration: 2000
              });
            });
        });
      } else {
        that.$message.warning({
          showClose: true,
          message: "",
          duration: 1000
        });
      }
    },

//
    delItem(index, row) {
      let that = this;
      this.$confirm("?", "", {
        type: "warning"
      })
        .then(() => {
          that.loading = true;
          let postId = row.id;
          let url = that.HOST + "/information/manager/deleteInformation";
          let params = Qs.stringify({
            ids: postId
          });
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(response => {
              if (response.data.result == "success") {
                that.loading = false;
                that.$message.success({
                  showClose: true,
                  message: "",
                  duration: 1500
                });
                that.search();
              } else {
                let err = response.data.message;
                that.loading = false;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
              }
            })
            .catch(function(error) {
              that.loading = false;
              console.log(error);
              that.$message.error({
                showClose: true,
                message: "",
                duration: 2000
              });
            });
        })
        .catch(() => {});
    },
    //
    selsChange: function(sels) {
      this.sels = sels;
    },
    //
    batchDeleteItems() {
      let that = this;
      this.$confirm("", "", {
        type: "warning"
      })
        .then(() => {
          that.loading = true;
          let ids = that.sels.map(item => item.id).toString();
          let url = that.HOST + "/information/manager/deleteInformation";
          let params = Qs.stringify({
            ids: ids
          });
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(response => {
              if (response.data.result == "success") {
                that.loading = false;
                that.$message.success({
                  showClose: true,
                  message: "",
                  duration: 1500
                });
                that.search();
              } else {
                let err = response.data.message;
                that.loading = false;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
              }
            })
            .catch(function(error) {
              that.loading = false;
              console.log(error);
              that.$message.error({
                showClose: true,
                message: "",
                duration: 2000
              });
            });
        })
        .catch(() => {});
    },

//
    handleSearch() {
      this.total = 0;
      this.page = 1;
      this.search();
    },
    //
    keyUpSubmit(e) {
      let key = window.event.keyCode;
      if (key === 13) {
        this.handleSearch()
      }
    },
    //
    search() {
      // console.log(this.currentTab);
      let that = this;

      let index = that.initFormRules.findIndex(
        menu => menu.name === that.currentTab
      );
      // console.log(index);
      let url = that.HOST + "/common/search";
      that.categoryId = that.initFormRules[index].categoryId;
      let content = that.filters.content.trim();

      if (that.categoryId != "") {
        let data = {
          pageNum: that.page, //1
          categoryId: that.categoryId, //,
          content: content,
          channel: 1
        };
        //loading
        that.loading = true;
        //get
        that.axios
          .get(url, {
            params: data
          })
          .then(response => {
            that.items = response.data.message.results.list;
            that.items.forEach((item, index) => {
              that.items[index].postDate = item.postDate.slice(0, 10);
            });
            that.total = response.data.message.results.total;
            that.pageSize = response.data.message.results.pageSize;
            
            that.loading = false;
          })
          .catch(response => {
            that.loading = false;
            that.items = [];
            that.total = 0;
            that.$message.error({
              showClose: true,
              message: "",
              duration: 2000
            });
          });
      } else {
        that.items = [];
        that.total = 0;
        that.$message.error({
          showClose: true,
          message: "",
          duration: 2000
        });
      }
    },

//
    //
    showEditDialog(index, row) {
      let that = this;
      if(row.img){that.editCoverShow=true;}
      this.editFormVisible = true;
      this.editForm = Object.assign({}, row);
      row.content.forEach((item,index) => {
      that.editFormContent[index].content = item.content.replace(/<\/div>/g, '</p>').replace(/<div/g, '<p'); //vue2-editorv-modeldiv
      });
    },
    //
    editImgUpload(e){
      let that = this;
      // let params = Qs.stringify({
      //     imgs: file,
      //   });
      let url = that.HOST +"/common/uploadImg"
      let formData = new FormData();
      formData.append("imgs", e.file);
      that.axios({
        url: url,
        method: 'POST',
        headers: {'Content-Type': 'multipart/form-data'},
        data: formData
      })
      .then(response => {
        if(response.data.result == "success"){
          that.editCoverShow = true;
          that.editForm.img = response.data.message;
        }
        else{
          that.$message.error({
            showClose: true,
            message: "",
            duration: 2000
        });
        }
      })
      .catch((err) => {
        console.log(err);
        that.$message.error({
          showClose: true,
          message: "",
          duration: 2000
        });
      })      
    },
    //
    editSubmit(){
      let that = this;
      let para = Object.assign({}, this.editForm);
      if (para.title && para.id) {
        that.$confirm("", "", { type: "warning" }).then(() => {
          let url = that.HOST + "/information/manager/updateInformation";
          let params = Qs.stringify({
            url: para.url,
            id: para.id,
            img:para.img,
            title: para.title,
            source: para.source,
            // postDate: para.postDate,
            // categoryId: that.categoryId
            content:that.editrFormContent}
            ,{arrayFormat: 'indices', allowDots: true})
          that.loading = true;
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(function(response) {
              that.loading = false;
              if (response.data.result == "success") {
                that.$message.success({
                  showClose: true,
                  message: "",
                  duration: 1500
                });
                that.editFormVisible = false;
                that.search();
              } else {
                let err = response.data.message;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
                that.editFormVisible = false;
              }
            })
            .catch(response => {
              that.loading = false;
              that.$message.error({
                showClose: true,
                message: "",
                duration: 2000
              });
            });
        });
      } else {
        that.$message.warning({
          showClose: true,
          message: "",
          duration: 1000
        });
      }
    }
  },
  activated() {
    this.handleSearch();
  },
  watch: {
    //
    isRequested() {
      this.handleSearch();
      // console.log(this.isRequested);
    }
  },
  components:{VueEditor}
};
</script>
<style lang="scss">
.el_b_form input{
  width: 50%!important;
}
.editor1 #quill-container{
  max-height: 1000px;
  overflow: auto;
}
.ql-snow .ql-picker{
  height: auto!important;
  margin-top:-6px; 
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 108px;
  height: 108px;
  line-height: 108px;
  text-align: center;
}
.avatar {
  width: 108px;
  height: 108px;
  display: block;
}
.avatar2{
  @extend .avatar;
}
</style>


