<template>
  <div id="objEdit">
    <el-row class="warp">
      <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="">
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
          <el-form :inline="true" :model="filters">
            <el-form-item>
              <el-input v-model="filters.content" placeholder="" @keyup.enter.native="handleSearch"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="handleSearch"></el-button>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="showAddDialog"></el-button>
            </el-form-item>
          </el-form>
        </el-col>

        <!---->
        <el-table :data="items" highlight-current-row @selection-change="selsChange" style="width: 100%; margin-bottom:10px;">
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column type="index" width="60"></el-table-column>
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label="[]">
                  <div v-html="props.row.introduction" style="display:block;"></div>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column prop="name" label="" sortable></el-table-column>
          <!-- <el-table-column prop="source" label="" width="100" sortable></el-table-column>
          <el-table-column prop="postDate" label="" width="150" sortable></el-table-column> -->
          <el-table-column label="" width="200">
            <template slot-scope="scope">
              <el-button size="small" @click="showEditDialog(scope.$index,scope.row)"></el-button>
              <el-button type="danger" @click="delItem(scope.$index,scope.row)" size="small"></el-button>
            </template>
          </el-table-column>
        </el-table>

        <!---->
        <el-col :span="24" class="toolbar">
          <el-button type="danger" @click="batchDeleteItems" :disabled="this.sels.length===0"></el-button>
          <el-pagination layout="prev, pager, next" background @current-change="handleCurrentChange" :page-size="pageSize" :total="total" style="float:right;">
          </el-pagination>
        </el-col>

        <!--  -->
        <el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="true" top="8vh" :modal-append-to-body="false">
          <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
            <el-form-item label="" prop="name">
              <el-input v-model="editForm.name" auto-complete="off"></el-input>
            </el-form-item>
            <!-- <el-form-item label="" prop="source">
              <el-input v-model="editForm.source" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="" prop="postDate">
              <el-date-picker type="date" placeholder="" v-model="editForm.postDate"></el-date-picker>
            </el-form-item> -->
            <el-form-item label="" prop="introduction">
              <vue-editor class="editor1" :editorToolbar="customToolbar" v-model="editForm.introduction"></vue-editor>
              <!-- <el-input type="textarea" v-model="editForm.introduction" :rows="8"></el-input> -->
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click.native="editFormVisible = false"></el-button>
            <el-button type="primary" @click.native="editSubmit"></el-button>
          </div>
        </el-dialog>

        <!---->
        <el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="true" top="8vh" :modal-append-to-body="false">
          <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
            <el-form-item label="" prop="name">
              <el-input v-model="addForm.name" auto-complete="off"></el-input>
            </el-form-item>
            <!-- <el-form-item label="" prop="source">
              <el-input v-model="addForm.source" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="" prop="postDate">
              <el-date-picker type="date" placeholder="" v-model="addForm.postDate"></el-date-picker>
            </el-form-item> -->
            <el-form-item label="" prop="introduction">
              <vue-editor class="editor1" :editorToolbar="customToolbar" v-model="addForm.introduction"></vue-editor>
              <!-- <el-input type="textarea" v-model="addForm.introduction" :rows="8"></el-input> -->
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click.native="addFormVisible = false"></el-button>
            <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
          </div>
        </el-dialog>

      </el-col>
    </el-row>
  </div>
</template>
<script>
import API from "@/api/api_admin";
import Qs from "qs";
import { VueEditor } from 'vue2-editor';

export default {
  name: "objEdit",
  components:{VueEditor},
  props: ["currentTab", "isRequested"],
  data() {
    return {
      categoryId: 0,
      //
      filters: {
        content: ""
      },
      //
      items: [
        {
          id: "",
          name: "",
          postDate: "2018-08-20",
          source: "",
          content:
            "http://www.gdei.gov.cn/ywfl/jscx/201808/t20180820_130227",
          introduction: "asdasdasd"
        }
      ],
      total: 1, //
      pageSize: 12, //
      page: 1, //
      // limit: 10,
      loading: false, //loading  
      sels: [], //
      //
      //categoryId
      initFormRules: [
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "get",
          categoryId: 6
        },
        {
          name: "",
          //method: "get",
          categoryId: 12
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "get",
          categoryId: 7
        },
        {
          name: "",
          //method: "get",
          categoryId: 9
        },
        {
          name: "",
          //method: "get",
          categoryId: 10
        },
        {
          name: "",
          //method: "get",
          categoryId: "18"
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "get",
          categoryId: 5
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "get",
          categoryId: 8
        },
        {
          name: "",
          //method: "",
          categoryId: 2
        },
        {
          name: "",
          //method: "",
          categoryId: 1
        },
        {
          name: "",
          //method: "",
          categoryId: 3
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: 4
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "get",
          categoryId: "16"
        },
        {
          name: "",
          //method: "get",
          categoryId: 11
        },
        {
          name: "",
          //method: "get",
          categoryId: "17"
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "",
          //method: "",
          categoryId: ""
        },
        {
          name: "1",
          //method: "",
          categoryId: ""
        },
        {
          name: "2",
          //method: "",
          categoryId: ""
        },
        {
          name: "3",
          //method: "",
          categoryId: ""
        }
      ],
      customToolbar: [
        // [{ 'size': ['small', false, 'large', 'huge'] }],  
        [{ 'header': [false,1, 2, 3, 4, 5, 6] }], 
        ['bold', 'italic', 'underline','strike'],['clean'],
        [{ 'align':""},{ 'align':"center"},{ 'align':"right"},{ 'align':"justify"}],
        [{ 'list': 'ordered'}, { 'list': 'bullet' }],
        [{ 'indent': '-1'}, { 'indent': '+1' }],
        ['blockquote', 'code-block'],
        ['image','link', ], 
      ],
      //
      editFormVisible: false, //
      // 
      editFormRules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        // source: [{ required: false, message: "", trigger: "blur" }],
        introduction: [{ required: true, message: "", trigger: "blur" }],
        // postDate: [
        //   { required: true, message: "", trigger: "blur" }
        // ]
      },
      //refdom
      editForm: {
        id: 0,
        name: "",
        // source: "",
        // postDate: "",
        introduction: "asdasdasd"
      },
      //
      addFormVisible: false, //
      addLoading: false,
      // 
      addFormRules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        // source: [{ required: false, message: "", trigger: "blur" }],
        introduction: [{ required: true, message: "", trigger: "blur" }],
        // postDate: [
        //   { required: true, message: "", trigger: "blur" }
        // ]
      },
      //refdom
      addForm: {
        name: "",
        // source: "",
        // postDate: "",
        introduction: "asdasdasd"
      }
    };
  },
  methods: {
    //
    handleCurrentChange(val) {
      this.page = val;
      this.search();
    },
    //
    //
    showAddDialog() {
      this.addFormVisible = true;
      this.addForm = {
        name: "",
        introduction: ""
      };
    },
    //
    addSubmit() {
      let that = this;
      let para = Object.assign({}, this.addForm);
      if (para.name && para.introduction) {
        that.$confirm("", "", { type: "warning" }).then(() => {
          let url = that.HOST + "/introduction/manager/addIntroduction";
          let params = Qs.stringify({
            name: para.name,
            // source: para.source,
            // postDate: para.postDate,
            introduction: para.introduction,
            categoryId: that.categoryId
          });
          that.loading = true;
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(function(response) {
              that.loading = false;
              if (response.data.result == "success") {
                that.$message.success({
                  showClose: true,
                  message: "",
                  duration: 1500
                });
                that.addFormVisible = false;
                that.search();
              } else {
                let err = response.data.message;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
                that.addFormVisible = false;
              }
            })
            .catch(response => {
              that.loading = false;
              that.$message.error({
                showClose: true,
                message: "",
                duration: 2000
              });
            });
        });
      } else {
        that.$message.warning({
          showClose: true,
          message: "",
          duration: 1000
        });
      }
    },
    //
    delItem(index, row) {
      let that = this;
      this.$confirm("?", "", { type: "warning" })
        .then(() => {
          that.loading = true;
          let postId = row.id;

          let url = that.HOST + "/introduction/manager/deleteIntroduction";
          let params = Qs.stringify({
            ids: postId
          });
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(response => {
              if (response.data.result == "success") {
                that.loading = false;
                that.$message.success({
                  showClose: true,
                  message: "",
                  duration: 1500
                });
                that.search();
              } else {
                let err = response.data.message;
                that.loading = false;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
              }
            })
            .catch(function(error) {
              that.loading = false;
              console.log(error);
              that.$message.error({
                showClose: true,
                message: "",
                duration: 2000
              });
            });
        })
        .catch(() => {});
    },
    //
    selsChange: function(sels) {
      this.sels = sels;
    },
    //
    batchDeleteItems() {
      let that = this;
      this.$confirm("", "", {
        type: "warning"
      })
        .then(() => {
          that.loading = true;
          let ids = that.sels.map(item => item.id).toString();
          let url = that.HOST + "/introduction/manager/deleteIntroduction";
          let params = Qs.stringify({
            ids: ids
          });
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(response => {
              if (response.data.result == "success") {
                that.loading = false;
                that.$message.success({
                  showClose: true,
                  message: "",
                  duration: 1500
                });
                that.search();
              } else {
                let err = response.data.message;
                that.loading = false;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
              }
            })
            .catch(function(error) {
              that.loading = false;
              console.log(error);
              that.$message.error({
                showClose: true,
                message: "",
                duration: 2000
              });
            });
        })
        .catch(() => {});
    },
    //
    handleSearch() {
      this.total = 0;
      this.page = 1;
      this.search();
    },
    //
    search() {
      console.log(this.currentTab);
      let that = this;

      let index = that.initFormRules.findIndex(
        menu => menu.name === that.currentTab
      );
      // console.log(index);
      let url = that.HOST + "/common/search";
      that.categoryId = that.initFormRules[index].categoryId;
      let content = that.filters.content.trim();

      if (that.categoryId != "") {
        let data = {
          pageNum: that.page, //1
          categoryId: that.categoryId, //,
          content: content,
          channel: 3
        };
        //loading
        that.loading = true;
        //get
        that.axios
          .get(url, {
            params: data
          })
          .then(response => {
            that.items = response.data.message.results.list;
            // that.items.forEach((item, index) => {
            //   that.items[index].postDate = item.postDate.slice(0, 10);
            // });
            that.total = response.data.message.results.total;
            that.pageSize = response.data.message.results.pageSize;
            that.loading = false;
          })
          .catch(response => {
            that.loading = false;
            that.items = [];
            that.total = 0;
            that.$message.error({
              showClose: true,
              message: "",
              duration: 2000
            });
          });
      } else {
        that.items = [];
        that.total = 0;
        that.$message.error({
          showClose: true,
          message: "",
          duration: 2000
        });
      }
    },

    //
    //
    showEditDialog(index, row) {
      this.editFormVisible = true;
      this.editForm = Object.assign({}, row);
    },
    //
    editSubmit(){
      let that = this;
      let para = Object.assign({}, this.editForm);
      if (para.name && para.introduction) {
        that.$confirm("", "", { type: "warning" }).then(() => {
          let url = that.HOST + "/introduction/manager/updateIntroduction";
          let params = Qs.stringify({
            id: para.id,
            name: para.name,
            // source: para.source,
            // postDate: para.postDate,
            introduction: para.introduction,
            // categoryId: that.categoryId
          });
          that.loading = true;
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(function(response) {
              that.loading = false;
              if (response.data.result == "success") {
                that.$message.success({
                  showClose: true,
                  message: "",
                  duration: 1500
                });
                that.editFormVisible = false;
                that.search();
              } else {
                let err = response.data.message;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
                that.editFormVisible = false;
              }
            })
            .catch(response => {
              that.loading = false;
              that.$message.error({
                showClose: true,
                message: "",
                duration: 2000
              });
            });
        });
      } else {
        that.$message.warning({
          showClose: true,
          message: "",
          duration: 1000
        });
      }
    }
  },
  activated() {
    this.handleSearch();
  },
  watch: {
    //
    isRequested() {
      this.handleSearch();
      console.log(this.isRequested);
    }
  }
};
</script>
<style lang="scss" scoped>
</style>




